<template>
  <div>
    <div class="flex">
      <input
        type="search"
        results="5"
        name="word"
        id="Search"
        placeholder="输入公司名称"
        v-model="searchValue"
        @keyup.enter="searchAnimation"
        class="input_border"
      />
      <div
        class="search"
        style="border-radius: 0 20px 20px 0;"
        @click="search()"
      >搜索</div>
    </div>
    <div>
      <mt-navbar
        v-model="selected"
        @click.native="chooseSelected"
      >
        <mt-tab-item id="0">
          <span>待处理</span>
        </mt-tab-item>
        <mt-tab-item id="1">
          <span>已处理</span>
        </mt-tab-item>
      </mt-navbar>
      <div
        v-if="financeList.length > 0"
        class="scroll"
      >
        <div
          v-for="(item, i) in financeList"
          :key="i"
          class="info_style flex"
          @click="goNext(item)"
        >
          <div class="info_left">
            <div class="c_000">{{ item.payer }}</div>
            <div class="ff7">¥{{ item.realAmount }}</div>
            <div class="b2">{{ item.payTime }}</div>
          </div>
          <div class="info_right">
            <div
              class="comePay"
              v-if="selected == '0'"
              @click.stop="configPayed(item)"
            >确定收账</div>
            <div
              style="color:#B5B2B5;"
              v-if="selected == '1'"
            >已确定</div>
          </div>
        </div>
      </div>
      <div
        v-else
        class="nothing"
      >
        <img
          src="@/assets/hotelAudit/empty.png"
          alt=""
        >
      </div>
    </div>
  </div>
</template>
<script>
import { GetBusinessFinancialRecordUnconfirm, GetBusinessFinancialRecordBypayee, PostBusinessFinancialRecordConfirm } from '@/api/api_business_bsm';
import { Dialog, Toast } from 'vant';
export default {
    components: {},
    data() {
        return {
            listLoading: false, // 上滑列表加载（每一次上滑的时候）---加载中
            finishedList: false, // 上滑列表加载完了所有数据---没有更多了）
            pullLoading: false, // 下拉刷新加载动画---加载中
            financeList: [], // 列表数据
            dataForm: {
                searchValue: null
            },
            searchValue: null,
            selected: '0',
            hotelStatusObj: {}
        };
    },
    created() {
        this.$store.commit('setHeader', '对公确认');
        this.onLoadList();
    },
    methods: {
        searchAnimation() {
            this.dataForm.searchValue = this.searchValue;
            this.financeList = [];
            this.onLoadList();
        },
        search() {
            this.dataForm.searchValue = this.searchValue;
            this.financeList = [];
            this.onLoadList();
        },
        chooseSelected() {
            this.dataForm.searchValue = this.searchValue;
            this.financeList = [];
            this.onLoadList();
        },
        onLoadList() {
            if (this.selected == '0') {
                GetBusinessFinancialRecordUnconfirm(this.dataForm).done((res) => {
                    this.financeList = this.financeList.concat(res.data);
                    this.listLoading = this.pullLoading = false;
                    if (res.total == this.financeList.length) {
                        this.finishedList = true;
                    } else if (res.total > this.financeList.length) {
                        this.finishedList = false;
                    }
                });
            } else {
                GetBusinessFinancialRecordBypayee(this.dataForm).done((res) => {
                    this.financeList = this.financeList.concat(res.data);
                    this.listLoading = this.pullLoading = false;
                    if (res.total == this.financeList.length) {
                        this.finishedList = true;
                    } else if (res.total > this.financeList.length) {
                        this.finishedList = false;
                    }
                });
            }
        },
        configPayed(item) {
            Dialog.confirm({
                title: '确认到账?'
            })
                .then(() => {
                    PostBusinessFinancialRecordConfirm({ financialRecordId: item.id }).done((res) => {
                        Toast({
                            message: res.msg,
                            duration: 900
                        });
                        this.financeList = [];
                        this.onLoadList();
                    });
                })
                .catch(() => {});
        },
        goNext(item) {
            this.$router.push({
                path: '/finance-detail',
                query: {
                    id: item.id
                }
            });
        }
    }
};
</script>
<style scoped>
input {
    -webkit-appearance: none !important;
    border-radius: 20px 0 0 20px !important;
    text-indent: 2em;
    margin: 10px 0;
    width: 82%;
    height: 32px;
    border: 0;
    outline: none;
    box-sizing: border-box;
}
#Search::-webkit-search-cancel-button {
    display: none;
}
.input_border {
    font-size: 0.7rem;
}
.flex {
    display: flex;
    justify-content: center;
}
.search {
    border: none;
    font-size: 0.7rem;
    color: #fff;
    margin-top: 10px;
    display: inline-block;
    width: 15%;
    height: 32px;
    line-height: 32px;
    background-color: #ff7d41;
}
.search:active {
    opacity: 0.5;
}
.scroll {
    margin: 10px 0;
    overflow: auto;
    position: fixed;
    bottom: 0;
    top: 150px;
    right: 0;
    left: 0;
}
.info_style {
    background: #fff;
    padding: 10px 20px;
    margin-bottom: 10px;
    justify-content: space-between;
    font-size: 0.8rem;
    color: #000;
    align-items: center;
    line-height: 26px;
}
.info_left {
    text-align: left;
}
.info_right {
    text-align: right;
}
.ff7 {
    color: #ff7d41;
}
.b2 {
    color: #b2b2b2;
    font-size: 0.7rem;
}
.nothing {
    margin: 100px auto;
    line-height: 50px;
    font-size: 0.8rem;
}
.c_000 {
    font-weight: bold;
    font-size: 0.9rem;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.comePay {
    border: 1px solid #ff7d41;
    border-radius: 8px;
    padding: 0px 8px;
    font-size: 0.7rem;
    color: #ff7d41;
}
/deep/ .mint-navbar .mint-tab-item.is-selected {
    color: #ff7d41;
    border-bottom: 3px solid #ff7d41;
}
</style>



